.social-networks-list{
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 90%;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.social-networks-list li{
    display: inline;
    margin: 0 3rem;
}

@media (max-width: 395px){
    .social-networks-list{
        overflow: auto;
    }

    .social-networks-list li{
        margin: 0 0.5rem;
    }
}